var React = require('react');
var Day = require('./Day');
var InfiniteCalendar = require('react-infinite-calendar').default;
require('react-infinite-calendar/styles.css');
require('./css/LessonController.css');

var today = new Date();
var minDate = Number(new Date()) - (24 * 60 * 60 * 1000) * 0; // One day before today

var LessonController = React.createClass({
    getInitialState: function() {
        return {
            dateSelected: 'today'
        }
    },
    calendarClick: function(date) {
        this.setState({
            dateSelected: date.format("YYYY_MM_DD")
        });
    },
    onDayClick: function(time) {
        this.props.onClick(this.state.dateSelected, time);
    },
    render: function() {
        let email = document.getElementById('loginStatus').innerText; //the email logged in on dashboard.html, NOT A GOOD STRUCTURE
        let _lessons = this.props.lessons.filter(function(lesson) {
            return lesson.date === this.state.dateSelected && lesson.email === email;
        }.bind(this));

        return (
            <div>
                <InfiniteCalendar
                    width={'100%'}
                    height={120}
                    selectedDate={today}
                    rowHeight={30}
                    minDate={minDate}
                    keyboardSupport={true}
                    afterSelect={this.calendarClick } />
                <Day lessons={_lessons} onClick={this.onDayClick}/>
            </div>
        );
    }
});

module.exports = LessonController;



