import firebase from 'firebase';
var db = firebase.database();
var lessonRef = db.ref("lessons");

function get() {
    return lessonRef.once("value").then(function(snapshot) {
        let lessons = [];
        snapshot.forEach(function(lesson) {
            lessons.push(lesson.val());
        });
        return lessons;
    });
}

function add(lesson) {
    let pushKey = lessonRef.push().key;
    lesson.key = pushKey;
    lessonRef.child(pushKey).update(lesson);
}

function remove(lessonKey) {
    lessonRef.child(lessonKey).remove();
}

module.exports = {
    get: get,
    add: add,
    remove: remove
};


