import React, { Component } from 'react';
import logo from './logo.svg';
import LessonController from './components/LessonController';
import Lesson from './models/lesson';
import './App.css';
import LessonDP from './dataPipe/lessonDataPipe';

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            lessons: []
        };
        LessonDP.get().then(function(lessons) {
            this.setState({ lessons: lessons });
        }.bind(this));
    };

    onClick(date, time) {
        let exists = false;
        let lessons = this.state.lessons;
        let email = document.getElementById('loginStatus').innerText;
        for (var i = 0; i < lessons.length; i++) {
            if (lessons[i].date === date && lessons[i].time === time) {
                let localLesson = lessons.splice(i, 1);
                // delete
                LessonDP.remove(localLesson[0].key); //Firebase Remove
                exists = true;
                break;
            }
        }
        if (!exists) {
            // add
            let tempLesson = new Lesson({ date: date, time: time, id: date+'_'+time, email: email });
            LessonDP.add(tempLesson); //Firebase Add
            this.state.lessons.push(tempLesson);
        }
        this.setState(this.state)
    }

    render() {
        return (
            <div className="App">
                <LessonController lessons={this.state.lessons} onClick={this.onClick.bind(this) }/>
            </div>
        );
    }
}

export default App;


